@import "../../global.scss";

.contact {
          background-color: white;
          display: flex;
          flex-direction: row;

          @include mobile {
                    flex-direction: column;
          }

          

          .left {
                    flex: 0.6;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    h2 {
                              font-size: 30px;
                    }

                    form {
                              width: 100%;
                              height: 60%;
                              display: flex;
                              flex-direction: column;
                              align-items: center;
                              justify-content: space-around;
                              border-radius: 5px;

                              input {
                                        width: 700px;
                                        height: 30px;
                                        font-size: 14px;

                                        @include mobile {
                                                  width: 300px;
                                                  height: 20px;
                                        }
                              }

                              textarea {
                                        width: 700px;
                                        height: 200px;
                                        font-size: 14px;

                                        @include mobile {
                                                  width: 300px;
                                                  height: 100px;
                                        }
                              }

                              button {
                                        width: 250px;
                                        height: 30px;
                                        color: white;
                                        background-color: crimson;
                                        border: none;
                                        border-radius: 10px;
                                        font-weight: 500;
                                        cursor: pointer;

                                        &:focus {
                                                  outline: none;
                                        }
                              }

                              span {
                                        color: green;
                              }
                    }
          }
          .right {
                    flex: 0.4;
                    display: flex;
                    overflow: hidden;
                    margin-top: 25vh;
                    margin-bottom: 10vh;
                    margin-left: 35px;
                    margin-right: -40px;
                    

                    ul {
                              font-size: 15px;
                              
                    }

          }
}
