@import "../../global.scss";



.portfolio {
          background-color: white;
          display: flex;
          flex-direction: column;
          align-items: center;

          h1 {
                    font-size: 30px;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;

                    @include mobile {
                              font-size: 25px;
                    }
          }

        

          .container {
                    width: 75%;
                    height: 70%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: wrap;

                    @include mobile {
                              width: 100%;
                    }

                    

                    .item {
                              width: 600px;
                              height: 505px;
                              border-radius: 20px;
                              border: 1px solid rgb(240, 239, 239);
                              margin: 10px 20px;
                              display: flex;
                              align-items: center;
                              justify-content: center;
                              color: white;
                              position: relative;
                              transition: all .5s ease;
                              cursor: pointer;

                              @include mobile {
                                        width: 330px;
                                        height: 200px;
                              }

                              h1 {
                                        position: absolute;
                                        font-size: 20px;
                              }

                              img {
                                        width: 100%;
                                        height: 100%;
                                        object-fit: cover;
                                        z-index: 1;
                              }

                              &:hover {
                                        background-color: $mainColor;

                                        img {
                                                  opacity: 0.9;
                                                  z-index: 0;
                                        }
                              }
                    }
          }
}