.intro {
background-color: white;
display: flex;


.left {
          flex: 0.5;
          height:100%;
.header {
          margin-top: 25vh;
          margin-bottom: 30vh;
}

h1 {
          margin-left: 45px;
          margin-right: 35px;
          font-size: 45px;
          line-height: 100%;
          color: black;
          text-transform: uppercase;

}
h3 {
  margin-left: 45px;
  margin-right: 35px;
  font-size: 25px;
  line-height: 70%;
  color: black;
  text-transform: uppercase;

}

@media screen and (max-width: 767px) {
          
          flex:100%;

          h1 {
                    font-size: 9vw;
          }
}

.alignleft {
          text-align: left;
}

.alignright {
          text-align: right;
}

.animate {
          -webkit-animation: animate .5s cubic-bezier(0.2, 0.6, 0.2, 1);
          -moz-animation: animate .5s cubic-bezier(0.2, 0.6, 0.2, 1);
          animation: animate .5s cubic-bezier(.59, .01, .28, 1);
          -webkit-animation-fill-mode: backwards;
          -moz-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

@-webkit-keyframes animate {
          0% {
                    opacity: 0;
                    -webkit-transform: translateY(-50px);
          }

          100% {
                    opacity: 1;
                    -webkit-transform: translateY(0%);
          }
}

@keyframes animate {
          0% {
                    opacity: 0;
                    transform: translateY(-50px);
          }

          100% {
                    opacity: 1;
                    transform: translateY(0%);
          }
}

.delayheader {
          -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.delaycontent {
          -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
}

.right {
          flex: 0.4;
          margin-top: 20vh;
          margin-bottom: 10vh;
          margin-left: 40px;
          margin-right: -40px;
          text-align: center;

          p {
            font-size: 18px;
            letter-spacing: 3px;
          }

@media screen and (max-width: 767px) {
          flex: 0;

          h1 {
                    font-size: 15vw;
          }
}}
}
