@import "../../global.scss";

.menu {
          width: 100%;
          height: 100vh;
          opacity: 0.9;
          background-color: $mainColor;
          position: fixed;
          top: 70px;
          right: -100%;
          z-index: 2;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          transition: all 1s ease;

          &.active {
                    right: 0;
          }

          ul {
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    font-size: 30px;
                    font-weight: 300;
                    color: white;
                    width: -75%;

                    li {
                              margin-bottom: 25px;

                              a {
                                        font-size: inherit;
                                        color: inherit;
                                        text-decoration: none;
                              }

                              &:hover {
                                        font-weight: 500;
                              }
                    }
          }
}